<template>
  <div class="allo-menu">
    <el-card>
      <div slot="header">
        <span>分配菜单</span>
        <el-button style="float: right; padding: 3px 0" class="el-icon-close" type="text" @click='$router.back()'></el-button>
      </div>
      <el-tree
        show-checkbox
        ref='menu-tree'
        default-expand-all
        :data="menus"
        :props="defaultProps"
        node-key='id'
        :default-checked-keys="checkedKeys"
      ></el-tree>
      <div style="text-align: center">
        <el-button @click='resetChecked'>清空</el-button>
        <el-button type="primary" @click='onSave'>保存</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import Vue from 'vue'
import { getMenuNodeList, allocateRoleMenus, getRoleMenus } from '@/services/menu'
export default Vue.extend({
  name: 'AllocMenu',
  props: {
    roleId: {
      type: [String, Number],
      require: true
    }
  },
  data () {
    return {
      menus: [],
      defaultProps: {
        children: 'subMenuList',
        label: 'name'
      },
      checkedKeys: []
    }
  },
  created () {
    this.loadMenus()
    this.loadRoleMenus()
  },
  methods: {
    async loadRoleMenus () {
      const { data } = await getRoleMenus(this.roleId)
      this.getCheckedKeys(data.data)
    },
    getCheckedKeys (menus) {
      menus.forEach(menu => {
        if (menu.selected === true && !menu.subMenuList) {
          this.checkedKeys = [...this.checkedKeys, menu.id]
        }
        if (menu.subMenuList) {
          this.getCheckedKeys(menu.subMenuList)
        }
      })
    },
    async loadMenus () {
      const { data } = await getMenuNodeList()
      this.menus = data.data
    },
    async onSave () {
      // 拿到选中节点的id列表
      const menuIdList = this.$refs['menu-tree'].getCheckedKeys()
      console.log(menuIdList)
      // 请求提交保存
      await allocateRoleMenus({
        roleId: this.roleId,
        menuIdList
      })
      this.$message.success('操作成功')
      this.$router.back()
    },
    resetChecked () {
      this.$refs['menu-tree'].setCheckedKeys([])
    }
  }
})
</script>

